body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.slider-board {
  margin-top: 18px;
  width: 100%;
}

.slider-board div {
  display: flex;
  justify-content: center;
}

.slider-msg {
  font-size: 24px;
}

.slider-msg-2 {
  font-size: 20px;
}

.slider-msg-3 {
  font-weight: 500;
  text-decoration: underline;
  font-size: 24px;
}

.btn-new-game {
  margin-top: 20px;
}

.btn-new-game button {
  outline: 0;
  font-size: 16px;
  font-weight: 600;
  color: white;
  background: #45884e;
  border: none;
  padding: 1.2em 3em;
  transition: all 0.1s ease-out;
  box-shadow: inset 0 -8px 0 0 rgba(0, 0, 0, 0.2), 1px 1px 0 0 #4c776e,
    2px 2px 0 0 #4c776e, 3px 3px 0 0 #4c776e;
  margin: 5px;
}
.btn-new-game.smaller button {
  font-size: 14px;
  font-weight: 600;
  padding: 1.2em 2em;
  margin-top: 0px;
}
.btn-new-game.smaller-2 button {
  font-size: 12px;
  font-weight: 600;
  padding: 1.2em 2em;
  margin-top: 0px;
}

.speed-change-btn {
  border: none;
  color: white;
  background: #45884e;
  box-shadow: inset 0 -8px 0 0 rgba(0, 0, 0, 0.2), 1px 1px 0 0 #4c776e,
    2px 2px 0 0 #4c776e, 3px 3px 0 0 #4c776e;
  margin: 5px;
  padding: 0.4em 0.7em;
  transition: all 0.1s ease-out;
}

.btn-new-game button:hover {
  color: #91ff7f;
}

.btn-new-game button:active {
  color: rgb(156, 250, 6);
  box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.2), 1px 1px 0 0 #4c776e,
    2px 2px 0 0 #4c776e, 3px 3px 0 0 #4c776e, 4px 4px 0 0 #4c776e,
    5px 5px 0 0 #4c776e;
}

.slider-board div span,
.slider-board div div {
  background: #5f5e88;
  width: 46px;
  margin: 0;
  margin-bottom: 2px;
  margin-left: 2px;
  padding: 14px;
  display: inline-block;
  color: #fff;
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 0px 0 rgba(255, 255, 255, 0.4),
    inset 0 0 8px 0 rgba(0, 0, 0, 0.4), inset 0 0 3px 5px rgba(0, 0, 0, 0.05),
    2px 2px 4px 0 rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.slider-board div span.empty,
.slider-board div div.empty {
  width: 46px;
  margin: 0;
  padding: 14px;
  margin-bottom: 2px;
  margin-left: 2px;
  background: #282c34;
  box-shadow: none;
}
